
import utils from "../utils.js";
import config from "../config.js";

/**
 * @param {string} key <true>  key
 * @param {string} value <true> 
 * @param {number} expire <false>  60*60
 */
export default (key, value, expire = 0) => {
  if (value === "" || value === null || value === undefined) {
    value = null;
  }

  if (isNaN(expire) || expire < 1) {
    console.log("Expire is not valid, expire reset to " + config.expire + "");
    expire = config.expire;
  }

  expire = expire * 1000;
  let data = {
    value: value, // 
    time: Date.now(), //
    expire: expire, // 
  };

  const encryptString = config.isEncrypt
    ? utils.encrypt(JSON.stringify(data))
    : JSON.stringify(data);

  window[config.type].setItem(utils.autoAddPrefix(key), encryptString);
};
